}

.main-wrapper .form-wrapper .field.flexed .with-icon input {
  width: 100%;
}

.main-wrapper .form-wrapper .field.flexed input {
  width: 43%;
}

.main-wrapper .form-wrapper .field .with-icon {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  width: 100%;
}

.main-wrapper .form-wrapper .field .with-icon input {
  width: 100%;
  padding-right: 25px;
}

.main-wrapper .form-wrapper .field .with-icon i {
  position: absolute;
  right: 10px;
