.main-wrapper .form-wrapper .forgot-description {
  text-align: end;
  margin-bottom: 20px;
  margin-top: 30px;
}

.main-wrapper .form-wrapper .forgot-description a {
  color: var(--active-color);
  text-decoration: none;
  font-size: 12px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.main-wrapper .form-wrapper .forgot-description a:hover {
  opacity: 0.5;
}

button {
  outline: none;
  cursor: pointer;
  border: none;
  background-color: var(--gray);
  color: var(--white);
  padding: 12px;
  text-align: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

button:hover {
  background-color: var(--active-hover);
  color: var(--muted-white);
}

button.medium {
  font-size: 15px;
}

button.large {
  font-size: 18px;
  width: 100%;
}

button.active {
  background-color: var(--active-color);
}

button.active:hover {
  background-color: var(--active-hover);
  color: var(--muted-white);
}

@media only screen and (max-width: 767px) {
  .main-wrapper .login-wrapper {
    width: 80%;
  }
  .main-wrapper .login-wrapper .form-wrapper .field.flexed {
