        flex-wrap: wrap;
  }
  .main-wrapper .login-wrapper .form-wrapper .field.flexed .with-icon {
    width: 100%;
    margin-bottom: 20px;
  }
  .main-wrapper .login-wrapper .form-wrapper .field.flexed input {
    width: 100%;
  }
}

.fade-appear {
  opacity: 0;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 300ms linear;
  transition: opacity 300ms linear;
}

.fade-exit {
  opacity: 1;
}

.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 300ms linear;
  transition: opacity 300ms linear;
}

.fade-exit-done {
  opacity: 0;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 300ms linear;
  transition: opacity 300ms linear;
}

:root {
  --primary-color: #2a3847;
  --active-color: #37b793;
  --active-hover: #3a8678;
  --white: #fff;
  --muted-white: #9ac7bb;
  --gray: #4e5d65;
  --red: #e00e0e;
}
